@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}

body {
  min-height: 100vh;
  /* overflow-y: hidden; */
}

.App {
  width: 100%;
  font-size: 20px;
  background-color: #fff8f0;
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff8f0;
  background-color: #264027;
}

  .menu__body {
    padding: 1em;
  }

    .menu__body h2 {
      margin-bottom: 1em;
    }

  .menu__option__group {
    display: flex;
  }

  .menu__option {
    width: 20vmin;
    height: 20vmin;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    align-items: center;
    background-color: #23ce6b;
  }

    .menu__option:hover, .menu__option:focus {
      background-color: #d36135;
    }

    .menu__option:not(:first-of-type) {
      margin-left: 1em;
    }

.header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;

  color: #fff8f0;
  background-color: #264027;
}

  .header__title {
    flex: 1 0;
  }

  .header--flex-start {
    justify-content: flex-start;
  }

.grid-wrapper {
  width: 100%;
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid {
  padding: 0.5em;

  display: grid;
  place-items: center;
  grid-gap: 0.1em;
  gap: 0.1em;
}

.button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1em;
}

.button:hover, .button:focus {
  cursor: pointer;
  color: #d36135;
}

.button--expanded {
  flex: 1 0;
  text-align: center;
}

.button--invisible {
  opacity: 0 !important;
}

@media only screen and (orientation: portrait) {
  .grid {
    --cell-dimension: calc((100vw - 1em) / var(--smaller-dimension) - 0.1em);

    width: 100%;
    /* height: max-content; */
    grid-auto-flow: row;
    grid-template-columns: repeat(var(--smaller-dimension), 1fr);
    grid-auto-rows: 1fr;
  }
}


@media only screen and (orientation: landscape) {
  .grid {
    --cell-dimension: calc((90vh - 1em) / var(--smaller-dimension) - 0.1em);

    width: -webkit-max-content;

    width: -moz-max-content;

    width: max-content;
    height: 90vh;
    grid-auto-flow: column;
    grid-template-rows: repeat(var(--smaller-dimension), 1fr);
    grid-auto-columns: 1fr;
  }
}
.cell {
  width: var(--cell-dimension);
  height: var(--cell-dimension);

  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;

  font-size: calc(var(--cell-dimension) * 0.6);
  color: #0c1b33;
  background-color: #23ce6b;

  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
  
.cell:hover {
  background-color: #264027;
}

.cell--revealed, .cell--revealed:hover {
  background-color: hsl(141, 60%, 80%) !important;
}

.cell > * {
  opacity: 0;
}

.cell--revealed > * {
  opacity: 1 !important;
}

.cell--flaged > * {
  font-size: calc(var(--cell-dimension) * 0.5);
  color: #d36135;
  opacity: 1 !important;
}
